<template>
  <el-container>
    <el-header style="font-size: 48px; background-color:azure; color:black;" class="el-icon-connection" >
      商城交流论坛
    </el-header>
    <el-main>
      <el-button @click="dialogVisible = true" type="primary" round>发布观点</el-button>
      <el-dialog
        title="发布帖子"
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose"
        custom-class="custom-dialog"
      >
        <el-form ref="form" :model="message" :rules="rules" label-width="80px">
          <el-form-item label="标题" prop="title">
            <el-input
              v-model="message.title"
              style="width: 84%"
              placeholder="标题不超过20个字"
              maxlength="20"
              show-word-limit
            ></el-input>
          </el-form-item>
          <el-form-item label="内容" prop="content">
            <el-input
              type="textarea"
              :autosize="{ minRows: 2, maxRows: 6 }"
              placeholder="请输入内容,小于420字"
              v-model="message.content"
              maxlength="420"
              show-word-limit
            ></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取消</el-button>
          <el-button type="primary" @click="submitForm('form')">发布</el-button>
        </span>
      </el-dialog>
      <div v-for="(item, index) in comments" :key="index" class="comment-item">
        <el-card shadow="hover" class="comment-card">
          <p class="comment-header">
            <span class="comment-title">{{ item.title }}</span>
            <span class="comment-user">发布者: {{ item.user }}</span>
            <span class="comment-time">{{ item.time }}</span>
            <span class="comment-id">文章ID: {{ item.collect_id }}</span>
            <!-- 新增：删除按钮 -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              round
              @click="deleteComment(index)"
              style="float: right; margin-left: 10px;"
            >删除</el-button>
          </p>
          <p class="comment-content">{{ item.content }}</p>
          <el-button
            type="primary"
            icon="el-icon-star-on"
            round
            @click="toggleLike(item)"
            style="float: right"
            :class="{ 'is-liked': item.isLiked }"
          >
            {{ item.isLiked ? '已点赞' : '点赞' }}
          </el-button>
          <br /><br />
          <hr />
        </el-card>
      </div>
    </el-main>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      User: {
        // name: main.user.username,
      },
      dialogVisible: false,
      message: {
        title: '',
        content: '',
        time: '',
        collect_id: '',
      },
      rules: {
        title: [
          { required: true, message: '请输入标题', trigger: 'blur' },
        ],
        content: [
          { required: true, message: '请输入内容', trigger: 'blur' },
        ],
      },
      comments: [
        { title: '商品质量真不错', user: '用户1', time: '2023-10-01 10:00', content: '刚刚购买的商品质量非常好，性价比很高！', collect_id: '001', isLiked: false },
        { title: '物流速度很快', user: '用户2', time: '2023-10-02 14:30', content: '快递员非常负责，送货上门也很及时。', collect_id: '002', isLiked: false },
        { title: '售后服务很到位', user: '用户3', time: '2023-10-03 09:15', content: '遇到问题后，客服人员耐心解答，解决问题的速度很快。', collect_id: '003', isLiked: false },
        { title: '推荐给朋友', user: '用户4', time: '2023-10-04 16:45', content: '这家商城的商品种类丰富，价格也很实惠，我一定会推荐给朋友。', collect_id: '004', isLiked: false },
        { title: '购物体验很棒', user: '用户5', time: '2023-10-05 11:20', content: '整体购物体验非常棒，从下单到收货都非常顺利。', collect_id: '005', isLiked: false },
        { title: '优惠活动很给力', user: '用户6', time: '2023-10-06 18:50', content: '参加了商城的优惠活动，省了不少钱。', collect_id: '006', isLiked: false },
        { title: '包装非常精美', user: '用户7', time: '2023-10-07 13:05', content: '收到的商品包装非常精美，感觉很有档次。', collect_id: '007', isLiked: false },
        { title: '商品图片与实物一致', user: '用户8', time: '2023-10-08 17:30', content: '商品图片与实物完全一致，没有任何问题。', collect_id: '008', isLiked: false },
        { title: '客服态度很好', user: '用户9', time: '2023-10-09 08:45', content: '客服人员非常热情，有问题随时可以联系到他们。', collect_id: '009', isLiked: false },
        { title: '物流服务很贴心', user: '用户10', time: '2023-10-10 12:10', content: '物流服务很贴心，每次都能按时送达。', collect_id: '010', isLiked: false },
      ],
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const now = new Date();
          const year = now.getFullYear();
          const month = String(now.getMonth() + 1).padStart(2, '0');
          const day = String(now.getDate()).padStart(2, '0');
          const hours = String(now.getHours()).padStart(2, '0');
          const minutes = String(now.getMinutes()).padStart(2, '0');
          const seconds = String(now.getSeconds()).padStart(2, '0');
          this.message.time = `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
          this.message.collect_id = `${this.User.name}${year}${day}${hours}${minutes}${seconds}`;
          this.comments.unshift({ ...this.message, isCollected: false });
          this.$message.success('发布成功');
          this.dialogVisible = false;
          this.$refs[formName].resetFields();
        } else {
          return false;
        }
      });
    },
    handleClose(done) {
      this.$confirm('确认关闭？')
        .then(() => {
          done();
        })
        .catch(() => {});
    },
    toggleLike(item) {
      item.isLiked = !item.isLiked;
      this.$message.success(item.isLiked ? '点赞成功' : '取消点赞成功');
    },
    deleteComment(index) {
      this.$confirm('确定要删除该评论吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }).then(() => {
        this.comments.splice(index, 1);
        this.$message.success('删除成功');
      }).catch(() => {
        this.$message.info('已取消删除');
      });
    },
  },
};
</script>

<style scoped>
.custom-dialog {
  border-radius: 10px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
.comment-item {
  margin-bottom: 20px;
}
.comment-card {
  border-radius: 8px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
.comment-header {
  font-weight: bold;
  margin-bottom: 10px;
}
.comment-title {
  color: #409EFF;
  margin-right: 20px;
}
.comment-user {
  color: #777;
  margin-right: 20px;
}
.comment-time {
  color: #999;
  margin-right: 20px;
}
.comment-id {
  color: #67C23A;
}
.comment-content {
  margin-bottom: 10px;
}
.is-liked {
  background-color: #409EFF;
  color: white;
}
</style>